import { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import {
  existingRequiredField,
  requiredField,
} from "../../../../Validation/functions/checkingRequiredField";
import Modal from "../../../../Ui/Modal/Modal";
import UserMemberContentWrapper from "../../../../Ui/UserMember/UserMemberContentWrapper";
import BorderBottom from "../../../../Ui/Border/BorderBottom";
import style from "../../../../UserStaffMember/FinanceManager/Income/Income.module.css";
import { assestsUserFinanceUrl } from "../../../../Services/UrlConfig/AssestsBaseUrl";
import { useStudentProfileTextCaseFormat } from "../../../../Hooks/ApiHooks/InstituteStudent/institute-student-api";
import QvipleLoader from "../../../../Loader/QvipleLoader";
import CreateButton from "../../../../Ui/Button/CreateButton";
import CustomSelect from "../../../../Ui/Select/CustomSelect";
const StudentFormOtherSetting = ({ onClose, instituteId, onRefetch, data }) => {
  const { t } = useTranslation();
  const [errorField, setErrorField] = useState("");
  const [disabled, setDisabled] = useState(false);

  const [state, setState] = useState({
    name_case_format_query: "",
  });

  const [studentProfileTextCaseFormat] = useStudentProfileTextCaseFormat();

  useEffect(() => {
    if (data) {
      setState((prev) => ({
        ...prev,
        name_case_format_query:
          data === "CAPS_FORMAT"
            ? "Capital case"
            : data === "SMALL_FORMAT"
            ? "Small case"
            : "Title case",
      }));
    }
  }, [data]);
  const onSelectFormat = (val) => {
    setState((prev) => ({
      ...prev,
      name_case_format_query: val,
    }));
  };
  const onAdd = () => {
    let validation = requiredField(state, []);
    if (existingRequiredField(validation) && instituteId) {
      setDisabled((pre) => !pre);
      studentProfileTextCaseFormat({
        id: instituteId,
        bodyParameter: {
          name_case_format_query:
            state.name_case_format_query === "Capital case"
              ? "CAPS_FORMAT"
              : state.name_case_format_query === "Small case"
              ? "SMALL_FORMAT"
              : "TITLE_FORMAT",
        },
      })
        .then(() => {
          onRefetch();
          setDisabled((pre) => !pre);
          onClose();
        })
        .catch({});
    } else {
      setErrorField(validation);
    }
  };
  return (
    <Modal onClose={onClose}>
      <div className={style.modal_container}>
        <UserMemberContentWrapper>
          <div className={style.modal_container_outer_header}>
            <div className={style.modal_container_header}>
              <h6>{t("other_form_setting")}</h6>
            </div>
            <img
              src={`${assestsUserFinanceUrl}/close.svg`}
              alt="close icon"
              onClick={onClose}
            />
          </div>
        </UserMemberContentWrapper>
        <BorderBottom />
        <UserMemberContentWrapper
          customStyle={{
            paddingTop: "0",
          }}
        >
          <CustomSelect
            selectLabel={t("student_profile_text_format")}
            selectedValue={t("select_student_profile_text_format")}
            options={["Capital case", "Small case", "Title case"]}
            onClick={onSelectFormat}
            defalutValue={state.name_case_format_query}
            errorShow={errorField["name_case_format_query"]}
          />

          <CreateButton label="update" onAction={onAdd} />
        </UserMemberContentWrapper>
        {disabled && <QvipleLoader />}
      </div>
    </Modal>
  );
};

export default StudentFormOtherSetting;
